import React from "react"
import styles from "./styles.module.scss"
import { graphql, useStaticQuery, Link } from "gatsby"
import Img from "gatsby-image"
import Zoom from "react-reveal/Zoom"

const About = () => {
  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "profile-photo.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  console.log("about", data)
  return (
    <div className={styles["about"]} id="about">
      <link href="https://emoji-css.afeld.me/emoji.css" rel="stylesheet" />
      <div className="container">
        <h1>About Me</h1>
        <Img
          fluid={data.placeholderImage.childImageSharp.fluid}
          className={styles["about-me-img"]}
        />
        <Zoom className={styles["about-texts"]}>
          <p>
            I am a software engineer and a product manager (Technical Business
            Analyst) at <Link to={"/experience/Indeed"}>Indeed</Link>. Now that
            I have been working on product management a lot more at work, I like
            to work on some hobby project such as this website to keep my
            development skill sharp. In my spare time, I like to learn about how
            to better improve myself financially, psychologically and
            physically.
          </p>
          <p>
            I have 2 cute cats from the same litter, their names are Smudgy and
            Stormy. Young and energetic, they deliver a good daily dosage of
            love, fun and racket{" "}
            <i className={`em em-cat ${styles["cat-icon"]}`} aria-label="CAT" />
            .
          </p>
          {/*<blockquote>*/}
          {/*"Yozu is the best client ever. From the very beginning, clear*/}
          {/*instructions were given on what to do, so we never had any*/}
          {/*misunderstandings during the course of the project. Quick replies,*/}
          {/*clear and friendly communication, daily regular feedbacks - all*/}
          {/*these things about Yozu. I wish everyone to have such good clients.*/}
          {/*Hope to work with you again." <br /> - Contracted Graphic Desinger*/}
          {/*</blockquote>*/}
        </Zoom>
      </div>
    </div>
  )
}

export default About
